<template>
  <div class="cms-list-layout">
    <cms-breadcrumb to="/memberGrade"  @click="rest">
      <li>{{$route.name}}</li>
    </cms-breadcrumb>
    <cms-prompt  collapse='true'>
      <li>显示所有会员等级的信息。</li>
      <li>可以添加会员等级、编辑会员等级、删除会员等级。</li>
    </cms-prompt>
    <section class="cms-list-query">
      <div class="query-group">
        <label>会员账户</label>
        <el-input v-model="queryParams.levelName" @keyup.native.enter="query"></el-input>
      </div>
      <div class="query-group">
        <label>邮箱</label>
        <el-input v-model="queryParams.email" @keyup.native.enter="query"></el-input>
      </div>
      <div class="query-group">
        <label>手机</label>
        <el-input v-model="queryParams.phone" @keyup.native.enter="query"></el-input>
      </div>
      <div class="query-group">
        <cms-button size="small" :colorPicker='true' @click="query">查询</cms-button>
      </div>
    </section>
    <section class="cms-batch">
      <cms-button icon="error" type="error" size="small" @click="deleteDatas(ids)">批量删除</cms-button>
    </section>
    <el-table :data="tableData" style="width: 100%" stripe  ref='dataTable'>
      <el-table-column type="selection" width="46" fixed='left'></el-table-column>
      <el-table-column prop="id" label="id" width="60"></el-table-column>
      <el-table-column prop=""  label="会员账户" width="110"></el-table-column>
      <el-table-column prop="" label="邮箱" min-width="220" align="center"></el-table-column>
      <el-table-column prop="" label="手机" min-width="220" align="center"></el-table-column>
      <el-table-column prop="" label="余额/积分" min-width="220" align="center">
        <div slot-scope="scope">
          <span>余额：</span>
          <span>积分：</span>
        </div>
      </el-table-column>
      <el-table-column prop="levelRights" label="成长值/会员等级" min-width="220" align="center">
        <div slot-scope="scope">
          <span>成长值：</span>
          <span>等级：{{scope.row.levelName}}</span>
        </div>
      </el-table-column>
    </el-table>
    <cms-pagination @change="getPage" :total='total'></cms-pagination>
  </div>
</template>

<script>
import { memberlevelAll, memberlevelDelete } from './../../../../api/users/level.js'
import list from '@/mixins/list'
var defaultData = {
  phone: '',
  roleName: '',
  email: '',
  all: '',
  page: 1,
  size: 15
}
export default {
  mixins: [list],
  data() {
    return {
      queryParams: Object.assign({}, defaultData)
    }
  },
  methods: {
    deleteData(id) { // 包装一层删除的操作
      this.deleteTableData(memberlevelDelete, id) // 这个才是真实的删除方法
    },
    deleteDatas(ids) {
      this.deleteTableDatas(memberlevelDelete, ids) // 这个才是真实的删除方法
    }
  },
  filters: {
    codeFiller(val) {
      switch (val) {
        case '1':
          return '购物福利 '
        case '2':
          return '生日特权 '
        default:
      }
    }
  },
  created() {
    this.getTableData(memberlevelAll, this.queryParams)
  }
}
</script>

<style>
</style>



